<template>
  <div class="container mx-auto p-4">
    <h1 class="text-2xl font-bold mb-4">會議室日曆</h1>

    <!-- 會議室說明 -->
    <div class="mb-4 relative">
      <a href="#" class="room-info-link text-blue-600 hover:underline" @mouseover="showImage = true" @mouseleave="showImage = false">
        會議室說明
      </a>
      <div v-if="showImage" class="room-image-preview">
        <img src="/meeting-room-booking/images/room_layout.jpg" alt="會議室平面圖" class="max-w-full max-h-80 object-contain" />
      </div>
    </div>
    
    <!-- 會議室篩選 -->
    <div class="mb-4">
      <label for="roomFilter" class="mr-2 text-sm font-medium text-gray-700">篩選會議室：</label>
      <select
        id="roomFilter"
        v-model="roomFilter"
        class="p-2 border border-gray-300 rounded-md"
        @change="applyFilter"
      >
        <option value="">全部會議室</option>
        <option value="A">大廳 A</option>
        <option value="B">會議室 B</option>
        <option value="C">會議室 C</option>
        <option value="D">會議室 D</option>
        <option value="E">會議室 E</option>
        <option value="F">會議室 F</option>
      </select>
    </div>
    
    <!-- FullCalendar -->
    <FullCalendar :options="calendarOptions" ref="calendar" />
    
    <!-- 管理者預約表單（僅登入時顯示） -->
    <div v-if="authStore.isAuthenticated" class="mt-6 bg-white p-6 rounded-lg shadow-md max-w-md">
      <h2 class="text-xl font-semibold mb-4">{{ formMode === 'add' ? '新增預約' : '編輯預約' }}</h2>
      <form @submit.prevent="handleSubmit">
        <div class="mb-4">
          <label for="title" class="block text-sm font-medium text-gray-700">會議標題</label>
          <input
            type="text"
            id="title"
            v-model="form.title"
            class="mt-1 block w-full p-2 border border-gray-300 rounded-md"
            required
          />
        </div>
        <div class="mb-4">
          <label for="room" class="block text-sm font-medium text-gray-700">會議室</label>
          <select
            id="room"
            v-model="form.room"
            class="mt-1 block w-full p-2 border border-gray-300 rounded-md"
            required
          >
            <option value="A">大廳 A</option>
            <option value="B">會議室 B</option>
            <option value="C">會議室 C</option>
            <option value="D">會議室 D</option>
            <option value="E">會議室 E</option>
            <option value="F">會議室 F</option>
          </select>
        </div>
        <div class="mb-4">
          <label for="start" class="block text-sm font-medium text-gray-700">開始時間</label>
          <input
            type="datetime-local"
            id="start"
            v-model="form.start"
            class="mt-1 block w-full p-2 border border-gray-300 rounded-md"
            required
          />
        </div>
        <div class="mb-4">
          <label for="end" class="block text-sm font-medium text-gray-700">結束時間</label>
          <input
            type="datetime-local"
            id="end"
            v-model="form.end"
            class="mt-1 block w-full p-2 border border-gray-300 rounded-md"
            required
          />
        </div>
        <div class="flex gap-2">
          <button
            type="submit"
            class="bg-blue-600 text-white p-2 rounded-md hover:bg-blue-700"
          >
            {{ formMode === 'add' ? '新增' : '更新' }}
          </button>
          <button
            v-if="formMode === 'edit'"
            @click="resetForm"
            type="button"
            class="bg-gray-500 text-white p-2 rounded-md hover:bg-gray-600"
          >
            取消
          </button>
          <button
            v-if="formMode === 'edit'"
            @click="deleteBooking"
            type="button"
            class="bg-red-600 text-white p-2 rounded-md hover:bg-red-700"
          >
            刪除
          </button>
        </div>
        <p v-if="error" class="mt-4 text-red-600">{{ error }}</p>
      </form>
    </div>
  </div>
</template>

<script>
import { ref, computed } from 'vue'
import { useAuthStore } from '@/stores/auth'
import { useBookingStore } from '@/stores/bookings'
import FullCalendar from '@fullcalendar/vue3'
import dayGridPlugin from '@fullcalendar/daygrid'
import timeGridPlugin from '@fullcalendar/timegrid'
import interactionPlugin from '@fullcalendar/interaction'

// 定義會議室與顏色的映射
const roomColors = {
  'A': '#FF6B6B', // 紅色
  'B': '#4ECDC4', // 青色
  'C': '#45B7D1', // 藍色
  'D': '#96CEB4', // 綠色
  'E': '#FFEEAD', // 黃色
  'F': '#D4A5A5'  // 粉紅
}

export default {
  name: 'CalendarView',
  components: { FullCalendar },
  setup() {
    const authStore = useAuthStore()
    const bookingStore = useBookingStore()
    const roomFilter = ref('')
    const calendar = ref(null) // 用於訪問 FullCalendar 實例

    // 載入預約數據
    bookingStore.fetchBookings()

    // 篩選後的事件
    const filteredEvents = computed(() => {
      if (!roomFilter.value) return bookingStore.bookings
      return bookingStore.bookings.filter(b => b.room === roomFilter.value)
    })

    // 日曆選項
    const calendarOptions = ref({
      plugins: [dayGridPlugin, timeGridPlugin, interactionPlugin],
      initialView: 'dayGridMonth',
      headerToolbar: {
        left: 'prev,next today',
        center: 'title',
        right: 'dayGridMonth,timeGridWeek,timeGridDay'
      },
      events: filteredEvents,
      editable: authStore.isAuthenticated,
      selectable: authStore.isAuthenticated,
      select: handleSelect,
      eventClick: handleEventClick,
      eventDidMount: (info) => {
        const room = info.event.extendedProps.room
        const color = roomColors[room] || '#CCCCCC' // 預設灰色，若無匹配顏色
        info.el.style.backgroundColor = color
        info.el.style.borderColor = color
      }
    })

    // 表單數據
    const form = ref({
      id: null,
      title: '',
      room: 'A',
      start: '',
      end: ''
    })
    const formMode = ref('add')
    const error = ref('')

    // 選擇日期範圍
    function handleSelect(selectInfo) {
      if (!authStore.isAuthenticated) return
      form.value = {
        id: null,
        title: '',
        room: 'A',
        start: selectInfo.startStr,
        end: selectInfo.endStr
      }
      formMode.value = 'add'
      error.value = ''
    }

    // 點擊事件
    function handleEventClick(clickInfo) {
      if (!authStore.isAuthenticated) return
      form.value = {
        id: clickInfo.event.id,
        title: clickInfo.event.title.replace(`${clickInfo.event.extendedProps.room} - `, ''), // 移除會議室前綴
        room: clickInfo.event.extendedProps.room,
        start: clickInfo.event.start.toISOString().slice(0, 16),
        end: clickInfo.event.end.toISOString().slice(0, 16)
      }
      formMode.value = 'edit'
      error.value = ''
    }

    // 提交表單
    async function handleSubmit() {
      if (!form.value.title || !form.value.room || !form.value.start || !form.value.end) {
        error.value = '請填寫所有欄位'
        return
      }
      if (new Date(form.value.end) <= new Date(form.value.start)) {
        error.value = '結束時間必須晚於開始時間'
        return
      }
      const booking = {
        title: `${form.value.room} - ${form.value.title}`,
        room: form.value.room,
        start: form.value.start,
        end: form.value.end
      }
      try {
        if (formMode.value === 'add') {
          console.log('Submitting new booking:', booking)
          await bookingStore.addBooking(booking)
        } else {
          console.log('Updating booking:', booking)
          await bookingStore.updateBooking(form.value.id, booking)
        }
        resetForm()
        // 手動觸發 FullCalendar 重新渲染
        if (calendar.value) {
          calendar.value.getApi().render()
        }
      } catch (error) {
        console.error('Submit error:', error)
        error.value = '操作失敗，請檢查伺服器連線'
      }
    }

    // 刪除預約
    async function deleteBooking() {
      if (confirm('確定要刪除此預約？')) {
        try {
          console.log('Deleting booking:', form.value.id)
          await bookingStore.deleteBooking(form.value.id)
          resetForm()
          if (calendar.value) {
            calendar.value.getApi().render()
          }
        } catch (error) {
          console.error('Delete error:', error)
          error.value = '刪除失敗，請檢查伺服器連線'
        }
      }
    }

    // 重置表單
    function resetForm() {
      form.value = {
        id: null,
        title: '',
        room: 'A',
        start: '',
        end: ''
      }
      formMode.value = 'add'
      error.value = ''
    }

    // 應用篩選
    function applyFilter() {
      if (calendar.value) {
        calendar.value.getApi().render()
      }
    }

    return {
      authStore,
      bookingStore,
      calendarOptions,
      calendar,
      form,
      formMode,
      error,
      roomFilter,
      handleSubmit,
      deleteBooking,
      resetForm,
      applyFilter
    }
  }
}
</script>

<style scoped>
.room-image-preview {
  display: none;
  position: absolute;
  z-index: 10;
  background: white;
  border: 1px solid #ccc;
  padding: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  max-width: 500px; /* 可選：限制容器最大寬度 */
}

.room-info-link:hover + .room-image-preview,
.room-image-preview:hover {
  display: block;
}

img {
  width: 100%; /* 確保圖片填滿容器 */
  height: auto; /* 保持縱橫比 */
  max-height: 80vh; /* 可選：限制最大高度，避免過大 */
  object-fit: contain; /* 確保等比例縮小且完整顯示 */
}
</style>