import { defineStore } from 'pinia'
import axios from 'axios'

const api = axios.create({
    baseURL: 'https://meeting-room-backen.onrender.com:5000/api'
})

export const useBookingStore = defineStore('bookings', {
    state: () => ({
        bookings: []
    }),
    actions: {
        async fetchBookings() {
            try {
                // 從伺服器獲取數據
                const response = await api.get('/bookings')
                this.bookings = response.data
                // 同步到 localStorage 作為緩存
                localStorage.setItem('bookings', JSON.stringify(this.bookings))
            } catch (error) {
                console.error('Fetch bookings error:', error)
                // 若伺服器失敗，從 localStorage 載入
                const storedBookings = localStorage.getItem('bookings')
                if (storedBookings) {
                    this.bookings = JSON.parse(storedBookings)
                } else {
                    // 初始化範例數據
                    this.bookings = [
                        {
                            id: '1',
                            title: '會議室 A - 企劃會議',
                            start: '2025-06-15T10:00:00',
                            end: '2025-06-15T12:00:00',
                            room: 'A'
                        },
                        {
                            id: '2',
                            title: '會議室 B - 技術討論',
                            start: '2025-06-15T14:00:00',
                            end: '2025-06-15T15:30:00',
                            room: 'B'
                        }
                    ]
                    localStorage.setItem('bookings', JSON.stringify(this.bookings))
                }
            }
        },
        async addBooking(booking) {
            this.bookings.push({
                id: Date.now().toString(),
                ...booking
            })
            await this.saveBookings()
        },
        async updateBooking(id, updatedBooking) {
            const index = this.bookings.findIndex(b => b.id === id)
            if (index !== -1) {
                this.bookings[index] = { ...this.bookings[index], ...updatedBooking }
                await this.saveBookings()
            }
        },
        async deleteBooking(id) {
            this.bookings = this.bookings.filter(b => b.id !== id)
            await this.saveBookings()
        },
        async saveBookings() {
            try {
                // 將完整 bookings 陣列發送到伺服器
                await api.post('/bookings', this.bookings)
                // 同步到 localStorage
                localStorage.setItem('bookings', JSON.stringify(this.bookings))
            } catch (error) {
                console.error('Save bookings error:', error)
                // 即使伺服器失敗，仍更新 localStorage
                localStorage.setItem('bookings', JSON.stringify(this.bookings))
            }
        }
    }
})